var common = require('./common');

var fs = require('fs');

var path = require('path');

common.register('mkdir', _mkdir, {
  cmdOptions: {
    'p': 'fullpath'
  }
}); // Recursively creates 'dir'

function mkdirSyncRecursive(dir) {
  var baseDir = path.dirname(dir); // Prevents some potential problems arising from malformed UNCs or
  // insufficient permissions.

  /* istanbul ignore next */

  if (baseDir === dir) {
    common.error('dirname() failed: [' + dir + ']');
  } // Base dir exists, no recursion necessary


  if (fs.existsSync(baseDir)) {
    fs.mkdirSync(dir, parseInt('0777', 8));
    return;
  } // Base dir does not exist, go recursive


  mkdirSyncRecursive(baseDir); // Base dir created, can create dir

  fs.mkdirSync(dir, parseInt('0777', 8));
} //@
//@ ### mkdir([options,] dir [, dir ...])
//@ ### mkdir([options,] dir_array)
//@ Available options:
//@
//@ + `-p`: full path (will create intermediate dirs if necessary)
//@
//@ Examples:
//@
//@ ```javascript
//@ mkdir('-p', '/tmp/a/b/c/d', '/tmp/e/f/g');
//@ mkdir('-p', ['/tmp/a/b/c/d', '/tmp/e/f/g']); // same as above
//@ ```
//@
//@ Creates directories.


function _mkdir(options, dirs) {
  if (!dirs) common.error('no paths given');

  if (typeof dirs === 'string') {
    dirs = [].slice.call(arguments, 1);
  } // if it's array leave it as it is


  dirs.forEach(function (dir) {
    try {
      fs.lstatSync(dir);

      if (!options.fullpath) {
        common.error('path already exists: ' + dir, {
          continue: true
        });
      }

      return; // skip dir
    } catch (e) {} // do nothing
    // Base dir does not exist, and no -p option given


    var baseDir = path.dirname(dir);

    if (!fs.existsSync(baseDir) && !options.fullpath) {
      common.error('no such file or directory: ' + baseDir, {
        continue: true
      });
      return; // skip dir
    }

    try {
      if (options.fullpath) {
        mkdirSyncRecursive(path.resolve(dir));
      } else {
        fs.mkdirSync(dir, parseInt('0777', 8));
      }
    } catch (e) {
      if (e.code === 'EACCES') {
        common.error('cannot create directory ' + dir + ': Permission denied');
      } else {
        /* istanbul ignore next */
        throw e;
      }
    }
  });
  return '';
} // mkdir


module.exports = _mkdir;